.Nav{margin:0 20px;position:fixed;top:20px;z-index:999}.Nav #Nav__menu{background-color:#fff;position:fixed;left:0;right:0;top:0;bottom:0;display:flex;justify-content:center;align-items:center;text-align:center;transition:all .5s}.Nav #Nav__menu .Nav__menu__main h1{margin:25px}.Nav #Nav__menu .Nav__menu__footer{position:fixed;bottom:0;left:0;right:0;justify-content:space-around;display:flex;margin:10px;font-size:1.5em}.Nav .Nav__menu__open{height:100vh}.Nav .Nav__menu__close{overflow:hidden;height:0;opacity:0%}#nav-icon4{width:calc(1.414213 * calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))));height:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));justify-self:right;transform:rotate(0deg);transition:.5s ease-in-out}#nav-icon4 span{display:block;position:absolute;height:2px;width:100%;background:#2a2a2a;border-radius:3px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}#nav-icon4 span:nth-child(1){top:0px;transform-origin:left center}#nav-icon4 span:nth-child(2){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 2);transform-origin:left center}#nav-icon4 span:nth-child(3){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 1);transform-origin:left center}#nav-icon4.open span:nth-child(1){transform:rotate(45deg);top:0px;left:0px}#nav-icon4.open span:nth-child(2){width:0%;opacity:0}#nav-icon4.open span:nth-child(3){transform:rotate(-45deg);top:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));right:0px}
.cursor{width:40px;height:40px;border:2px solid #ccc;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#ccc}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#ccc}html,html *,body *{cursor:none}
.Footer{padding:0 20px;background-color:#ccc;padding-top:30px}.Footer .contact__description{display:flex;flex-direction:column;align-items:flex-end}.Footer .contact__description h2{margin-top:30px;margin-bottom:30px;text-align:right;max-width:calc(300px + (500 - 300) * ((100vw - 300px) / (1600 - 300)))}.Footer .contact__description a{color:#fff}.Footer .contact__description a:hover{color:#2a2a2a}.Footer .copyright{padding:15px;display:flex;justify-content:center}@media only screen and (min-width: 1000px){.Footer{padding:0 150px}}
.About .About__title{display:flex;flex-direction:column;align-items:center}.About .About__title h1{font-size:calc(40px + (100 - 40) * ((100vw - 300px) / (1600 - 300)))}.About .About__subtitle{margin:50px 0;color:#ccc;text-align:center}.About .About__description{max-width:700px;margin:0 auto}.About .About__description p{margin-top:25px}.About .About__description a{font-weight:700;text-decoration:underline}
.Work{margin-top:150px}.Work h1,.Work h2{text-align:center}.Work .Work__category{display:flex;justify-content:space-around;align-items:center;margin:50px 0}.Work .Work__category svg{color:#ccc;height:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));width:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));transition:.5s color}.Work .Work__category .active{color:#2a2a2a}
.Work__grid{display:grid;grid-gap:20px;gap:20px;grid-template-columns:1fr 1fr}.Work__grid .Work__item{display:flex;flex-direction:column;justify-content:center}.Work__grid .Work__item img{width:100%;height:100%;object-fit:cover}.Work__grid .Work__item .Work__item__description{margin-top:15px}@media only screen and (min-width: 1000px){.Work__grid{grid-template-columns:1fr 1fr 1fr}}
.resume h1{text-align:center}.resume .resume__description{margin:calc(50px + (100 - 40) * ((100vw - 300px) / (1600 - 300))) 0;text-align:center}.resume .resume__section:nth-child(odd){background-color:#f5f5f5}.resume .resume__section{display:grid;grid-template-columns:1fr;padding:20px}.resume .resume__section h2{text-align:center;margin-bottom:10px}.resume .resume__item{margin-bottom:25px}.resume .resume__item .resume__item__inner{font-style:italic}.resume .resume__item .resume__item__inner .resume__role{font-weight:700}.resume .resume__item:last-child{margin-bottom:0}@media only screen and (min-width: 700px){.resume .resume__description{text-align:start}.resume .resume__section{display:grid;grid-template-columns:1fr 3fr}.resume .resume__section h2{text-align:start;margin-bottom:0}.resume .resume__item__inner{margin-left:25px}}
.work__page .work__page__title{padding:25px;display:flex;flex-direction:column;justify-content:center;align-items:center}.work__page .work__page__title h1{font-family:"Space Grotesk",sans-serif;font-size:calc(30px + (75 - 30) * ((100vw - 300px) / (1600 - 300)))}.work__page .work__page__content{display:grid;grid-template-columns:1fr}.work__page .work__page__content .work__images img{width:100%}.work__page .work__page__content .work__text p{margin-top:10px}.work__page .back__button{margin-top:50px}
.blog h1{text-align:center}.blog .blog__grid{margin:25px 0}.blog .blog__grid .blog__card{display:grid;grid-template-columns:2fr 1fr;border:solid #000 1px;margin:25px 0}.blog .blog__grid .blog__card .blog__card__text{padding:15px}.blog .blog__grid .blog__card .blog__card__thumbnail{background-color:#ccc}

.App{margin:calc(50px + (100 - 40) * ((100vw - 300px) / (1600 - 300))) 20px}@media only screen and (min-width: 1000px){.App{margin:0 150px;margin:initial 150px}}
*{padding:0;margin:0;box-sizing:border-box;scroll-behavior:smooth}body{overflow-x:hidden;font-family:"Poppins",sans-serif;color:#2a2a2a;font-size:calc(12px + (15 - 12) * ((100vw - 300px) / (1600 - 300)))}body h1{font-family:"Space Grotesk",sans-serif;font-size:calc(30px + (75 - 30) * ((100vw - 300px) / (1600 - 300)))}body h2{font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)))}body a{color:#2a2a2a;transition:color .2s;text-decoration:none}body a:hover{color:#ccc}.noscroll{overflow-y:hidden}::selection{background:#ccc}::-moz-selection{background:#ccc}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:rgba(0,0,0,.5)}::-webkit-scrollbar-thumb{border-radius:0px;background:#2a2a2a}
